/* store.js */
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// 기본 저장소
const baseStore = {
    namespaced: true,
    state: {
        isShowBar: true,
        isLoading: false
    },
    getters: {
        getIsShowBar(state){
            return state.isShowBar;
        },
        getIsLoading(state){
            return state.isLoading;
        }
    },
    mutations: {
        setIsShowBar(state, bTrue){
            state.isShowBar = bTrue;
        }, setIsLoading(state, bTrue){
            state.isLoading = bTrue;
        }
    
    },
    actions : {
            setIsShowBar({commit}, bTrue) {
                commit('setIsShowBar',bTrue);
            },
            setIsLoading({commit}, bTrue) {
                console.log("setIsLoading");
                commit('setIsLoading',bTrue);
            }
        }
    }

export default  new Vuex.Store({
    modules : {
        baseStore : baseStore
    }
});
